import axios from 'axios';

const rootAPI = 'http://localhost:55159/api/';
const pathAPIJabatan = 'MasterJabatans';

const Get = (ServiceName, data)=> {
    let param = '';
    if(data === undefined ){
      param = '';
    }else{
      param = data;
    }
    const promise = new Promise((resolve, reject) =>{

        axios.get(`${rootAPI}${ServiceName}${param}`)
        .then((result) =>{
          resolve(result.data);
        }, (error) =>{
          reject(error)
        })
    })

    return promise;
}

const PostNoJson = (serviceName, data) => {
    const promise = new Promise((resolve, reject) =>{
      axios.post(`${rootAPI}${serviceName}?${data}`)
        .then((result) => {
          resolve(result.data)
        }, (error)=> {
          reject(error)
        })

    });

    return promise;

}

/** GET */
const GetJabatans = () => Get(`${pathAPIJabatan}/GetJabatans`);
const GetJenisLokpris = () => Get(`MasterJenisLokpris/GetJenisLokpris`);
const GetPegawais = () => Get(`MasterPegawais/GetPegawais`);

/** POST  */
const AddMonev = (dataParam) => PostNoJson('Mnvs/AddMonev/',dataParam);

const SvcMonev = {
  GetJabatans,
  GetJenisLokpris,
  GetPegawais,
  AddMonev
}

export default SvcMonev;
