import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CImg,
  CRow,
  CForm,
  CFormGroup,
  CLabel,
  CInput,
  CSelect,
  CButton,
  CModal,
  CModalBody,
  CModalHeader,
  CModalTitle,
} from "@coreui/react";
import React, { useEffect, useState } from "react";
import MaterialTable from "material-table";
import Stepper from "react-stepper-horizontal";
import Select from "react-select";
import makeAnimated from "react-select/animated";
import SvcMonev from "./services-monev";
import swal from "sweetalert";

import SVCMasterWil from "../master-wil/services-wil";
import BuildFormDynamic from "./BuildFormDynamic";
import { useDispatch, useSelector } from "react-redux";
/**\
 * import all action redux
 */
import {
  SetDropdownJenisLokpriAction,
  setDataComboProv,
  setDataDropdownKabupatenAction,
  setDataDropdownKecamatanAction,
  SetDropdownPgwKecamatansByKecamatanAction,
  SetDropdownPgwKecamatansByJabatanAction,
  GetPegawiKecamatanAction,
} from "src/common/redux/action";
const AddMonev = () => {
  const dispatch = useDispatch();
  /**
   * inisialize reducer
   */
  const {
    dataComboJenisLokpri,
    dataComboProv,
    dataComboKabupaten,
    dataComboKecamatan,
    dataCmbPgwKecamatansByKecamatanReducer,
    dataCmbPgwKecamatansByJabatanReducer,
  } = useSelector((state) => state.dropdownReducer);

  const { setfisrtAddMonevReduce } = useSelector(
    (state) => state.addMonevReducer
  );



  const animatedComponents = makeAnimated();

  const [GetBtnLanjutkan, SetBtnLanjutkan] = useState(false);
  const [PostLanjutkanFirts, SetPostLanjutkanFirts] = useState({
    IdUser: 1,
    IdPegawai: 0,
    IdJenisLokpri: 0,
  });


  const GetLoadForm = () => {
    dispatch(setDataComboProv("DATA_CMB_PROV"));
    dispatch(SetDropdownJenisLokpriAction("DATA_CMB_LOKPRI"));

  };

  const provinsiChangeHandler = (event) => {
    let idProv = event.target.value;
    dispatch(setDataDropdownKabupatenAction("DATA_CMB_KAB",idProv));
  }

  const kabupatenChangeHandler = (event) => {
    let idKab =event.target.value;
    dispatch(setDataDropdownKecamatanAction("DATA_CMB_KEC", idKab));
  };

  const kecamatanChangeHandler = (event) => {
    let idKec = event.target.value;
    dispatch(
      SetDropdownPgwKecamatansByKecamatanAction(
        "DATA_CMB_PEGAWAI_KEC_BY_KEC",
        idKec
      )
    );
  }

  const JabatanChangeHandler = (e) => {
    let idJabatan = e.value;
    dispatch(
      SetDropdownPgwKecamatansByJabatanAction(
        "DATA_CMB_PEGAWAI_KEC_BY_JABATAN",
        idJabatan
      )
    );
  };

  const {
    IdPegawai,
    IdJenisLokpri,
    FullName,
    MobileNumber,
    Email,
    OfficeAddress,
  } = setfisrtAddMonevReduce;

  const PegawaiChangeHandler = (e) =>{

    let idPegawai = e.value;
    dispatch(GetPegawiKecamatanAction(idPegawai));
  }



  const handleSelectedKategori = (event) => {
    let setValueIdLokpri = event.target.value;
    let PostLanjutkanFirtsNew = { ...PostLanjutkanFirts };
    PostLanjutkanFirtsNew["IdJenisLokpri"] = parseInt(setValueIdLokpri);
    SetPostLanjutkanFirts(PostLanjutkanFirtsNew);
  };

  const [GetFormStepper, SetFormStepper] = useState({
    page: 0,
    steps: [],
    dataResult: [],
  });

  const nextPage = () => {
    let GetFormStepperNew = { ...GetFormStepper };
    GetFormStepperNew["page"] = GetFormStepper.page + 1;
    SetFormStepper(GetFormStepperNew);
  };
  const previousPage = () => {
    let GetFormStepperNew = { ...GetFormStepper };
    GetFormStepperNew["page"] = GetFormStepper.page + 1;
    SetFormStepper(GetFormStepperNew);
  };

  const handleLanjutkan = () => {
    let idUser = PostLanjutkanFirts["IdUser"];
    let idJenisLokpri = PostLanjutkanFirts["IdJenisLokpri"];

    if (IdPegawai === 0) {
      swal("Pegawai", "Harap Pilih Pegawai", "error");
    } else if (idJenisLokpri === 0) {
      swal("Lokpri", "Harap Pilih Jenis Lokpri", "error");
    } else {
      SetBtnLanjutkan(true);
      consumePostMonev1();
    }
  };

  const consumePostMonev1 = () => {
    let idUser = PostLanjutkanFirts["IdUser"];
    let idPegawai = PostLanjutkanFirts["IdPegawai"];
    console.log(idPegawai);
    let idJenisLokpri = PostLanjutkanFirts["IdJenisLokpri"];
    let setParam = `IdUser=${idUser}&IdPegawai=${idPegawai}&IdJenisLokpri=${idJenisLokpri}`;
    SvcMonev.AddMonev(setParam).then((result) => {
      if (result.isSuccess) {
        let GetFormStepperNew = { ...GetFormStepper };
        let stepArrTmp = [];
        result.data.jawabans.map((value) => {
          let objTitle = {
            title: value.variabel,
            dataTest: result.data.jawabans,
          };
          stepArrTmp.push(objTitle);
        });
        GetFormStepperNew["dataResult"] = result.data.jawabans;
        GetFormStepperNew["steps"] = stepArrTmp;
        return SetFormStepper(GetFormStepperNew);
      }
    });
  };

  useEffect(() => {
    GetLoadForm();
  }, []);

  return (
    <CRow>
      <CCol className="offset-md-1" md="10">
        <CCard>
          <CCardHeader>
            <CImg
              src={process.env.PUBLIC_URL + "/Icon.png"}
              className="col-md-3 d-flex justify-content-center"
            />
          </CCardHeader>
          <CCardBody>
            <CForm className="form-horizontal">
              {GetBtnLanjutkan == false ? (
                <div>
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="selectSm">Provinsi</CLabel>
                    </CCol>
                    <CCol xs="12" md="5">
                      <CSelect
                        name="provinsi"
                        id="provinsi"
                        onChange={provinsiChangeHandler}
                      >
                        <option value="0"> --Pilih Provinsi -- </option>
                        {dataComboProv
                          ? dataComboProv.map((val, index) => (
                              <option
                                key={val.idProvinsi}
                                value={val.idProvinsi}
                              >
                                {val.nama}
                              </option>
                            ))
                          : null}
                      </CSelect>
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="selectSm">Kabupaten</CLabel>
                    </CCol>
                    <CCol xs="12" md="5">
                      <CSelect
                        name="kabupaten"
                        id="kabupaten"
                        onChange={kabupatenChangeHandler}
                      >
                        <option value="0"> --Pilih Kabupaten -- </option>
                        {dataComboKabupaten.length > 0
                          ? dataComboKabupaten.map((val, idx) => (
                              <option
                                key={val.idKabupaten}
                                value={val.idKabupaten}
                              >
                                {val.nama}
                              </option>
                            ))
                          : null}
                      </CSelect>
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="selectSm">Kecamatan</CLabel>
                    </CCol>
                    <CCol xs="12" md="5">
                      <CSelect
                        name="kecamatan"
                        id="kecamatan"
                        onChange={(event) => kecamatanChangeHandler(event)}
                      >
                        <option value="0"> --Pilih Kecamatan -- </option>
                        {dataComboKecamatan.map((val, idx) => (
                          <option key={val.idKecamatan} value={val.idKecamatan}>
                            {" "}
                            {val.nama}
                          </option>
                        ))}
                      </CSelect>
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="pilihJabatan"> Pilih Jabatan</CLabel>
                    </CCol>
                    <CCol xs="12" md="5">
                      <Select
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        isMulti={false}
                        onChange={(e) => JabatanChangeHandler(e)}
                        options={dataCmbPgwKecamatansByKecamatanReducer}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="pilihNama"> Pilih Nama</CLabel>
                    </CCol>
                    <CCol xs="12" md="5">
                      <Select
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        isMulti={false}
                        options={dataCmbPgwKecamatansByJabatanReducer}
                        onChange={(e) => PegawaiChangeHandler(e)}
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="text-input">Alamat Kantor</CLabel>
                    </CCol>
                    <CCol xs="12" md="9">
                      <CInput
                        id="text-input"
                        name="text-input"
                        placeholder="Input Alamat"
                        value={OfficeAddress || ""}
                        disabled
                      />
                    </CCol>
                  </CFormGroup>
                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="email-input">Email </CLabel>
                    </CCol>
                    <CCol xs="12" md="5">
                      {console.log("Test value email" + Email)}
                      <CInput
                        type="email"
                        id="email-input"
                        name="email-input"
                        placeholder="Input Email"
                        autoComplete="email"
                        value={Email || ""}
                        disabled
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="disabled-input">No HP</CLabel>
                    </CCol>
                    <CCol xs="12" md="6">
                      <CInput
                        id="no-hp"
                        name="no-hp"
                        placeholder="Input No Hp"
                        value={MobileNumber}
                        disabled
                      />
                    </CCol>
                  </CFormGroup>

                  <CFormGroup row>
                    <CCol md="3">
                      <CLabel htmlFor="selectSm">Kategori</CLabel>
                    </CCol>
                    <CCol xs="12" md="5">
                      <CSelect
                        name="kategori"
                        id="kategori"
                        onChange={handleSelectedKategori}
                      >
                        <option value="0"> --Pilih Kategori -- </option>
                        {dataComboJenisLokpri.length !== 0
                          ? dataComboJenisLokpri.map((item) => (
                              <option
                                key={item.idJenisLokpri}
                                value={item.idJenisLokpri}
                              >
                                {item.nama}
                              </option>
                            ))
                          : null}
                      </CSelect>
                    </CCol>
                  </CFormGroup>

                  <CButton
                    type="button"
                    size="sm"
                    color="primary"
                    className="col-md-3 offset-md-4"
                    onClick={handleLanjutkan}
                  >
                    {" "}
                    Lanjutkan{" "}
                  </CButton>
                </div>
              ) : (
                <CCard>
                  <Stepper
                    steps={GetFormStepper.steps}
                    activeStep={GetFormStepper.page}
                  />
                  <div style={{ paddingTop: 10 }}>
                    {GetFormStepper.page === 0 ? (
                      <BuildFormDynamic
                        onSubmit={nextPage}
                        onClick={previousPage}
                        DataJson={GetFormStepper.dataResult}
                      />
                    ) : (
                      <BuildFormDynamic
                        onSubmit={nextPage}
                        onClick={previousPage}
                        DataJson={GetFormStepper.dataResult}
                      />
                    )}
                  </div>
                </CCard>
              )}
            </CForm>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
};

export default AddMonev;
