import axios from 'axios';

const rootAPI = process.env.REACT_APP_ROOT_API;
const rootPath = 'MasterWilOrgs/'
const urlApi = rootAPI+rootPath;

const Get = (ServiceName, data)=> {
    let param = '';
    if(data === undefined ){
      param = '';
    }else{
      param = data;
    }
    const promise = new Promise((resolve, reject) =>{

        axios.get(`${rootAPI}${ServiceName}${param}`)
        .then((result) =>{
          resolve(result.data);
        }, (error) =>{
          reject(error)
        })
    })

    return promise;
}

const PostNoJson = (serviceName, data) => {
    console.log(data)
    const promise = new Promise((resolve, reject) =>{
      axios.post(`${urlApi}${serviceName}?${data}`)
        .then((result) => {
          resolve(result.data)
        }, (error)=> {
          reject(error)
        })

    });

    return promise;

}

const PutTypeJson = (serviceName, data) => {
  const promise = new Promise((resolve, reject) =>{
      axios.post(`${urlApi}${serviceName}`, data)
        .then((result) => {
          resolve(result.data)
        }, (error)=> {
          reject(error)
        })

    });

    return promise;
}

/** GET */
const GetProvinsis = () => Get('MasterWilOrgs/GetProvinsis');
const GetProvinsi = (param) => Get('MasterWilOrgs/GetProvinsi',param);
const GetKabupatenByIdProv = (param) => Get('GetKabupatens',param);
const GetKecamatanByIdKab = (param) => Get('GetKecamatans',param);

/**POST */
const AddProvinsi = (data) => PostNoJson('AddProvinsi', data);

/**Put */
const EditProvinsiWeb = (data) => PutTypeJson('EditProvinsi', data);

const HapusProvinsi = (data) => PostNoJson('DeleteProvinsi', data);


const SVCMasterWil = {
  GetProvinsis,
  GetProvinsi,
  AddProvinsi,
  EditProvinsiWeb,
  HapusProvinsi,
  GetKabupatenByIdProv,
  GetKecamatanByIdKab
}

export default SVCMasterWil
